<template>
  <div class="content">
    <span class="color-main">标准查询</span>

    <el-input
      class="ml-10"
      placeholder="请输入搜索内容"
      v-model="searchValue"
      style="width: 30%"
      @input="handleSearchInfo"
      clearable
    >
      <el-button
        slot="append"
        icon="el-icon-search"
        @click="handleSeache()"
      ></el-button>
    </el-input>

    <hr />
    <div class="card-item" v-loading="loading" v-if="tableData.length">
      <el-card v-for="i in tableData" :key="i.id" style="margin-bottom: 20px">
        <div class="box-card">
          <span> {{ i.file_name }}</span>
          <el-button type="primary" size="small" @click="openFile(i.id)">
            打开
          </el-button>
        </div>
      </el-card>
    </div>
    <el-empty description="搜索标准文件" v-if="!tableData.length"> </el-empty>
  </div>
</template>

<script>
import { getAllFiles, downloadFile, searchFile } from '@/api/standard.js'
import { debounce } from '@/utils'

export default {
  name: 'StandardInfo',
  data () {
    return {
      searchValue: '',
      loading: false,
      tableData: [],
      currentTableData: []
    }
  },
  created () {
    // this.init();
    // console.log(this.$route.query);
  },
  activated () {
    // console.log(this.$route.query);
    // this.searchValue = this.$route.query.keywords;
    this.searchValue = this.$store.state.keywords
    this.handleSearchInfo()
  },
  methods: {
    async init () {
      this.loading = true
      try {
        const { result } = await getAllFiles()
        console.log(result)
        this.tableData = result
        this.currentTableData = result.slice(0, 10)
        this.loading = false
      } catch (error) {
        this.loading = false
        return error
      }
    },
    async openFile (id) {
      const res = await downloadFile(id)
      // window.location.href = "/api" + res;
      window.open(this.$url + res)
    },
    handleSearchInfo: debounce(async function () {
      if (!this.searchValue.trim().length) {
        this.currentTableData = []
        this.tableData = []
        return
      }
      this.loading = true
      const { result } = await searchFile(this.searchValue)
      this.loading = false
      this.tableData = result
      this.currentTableData = result.slice(0, 10)
      this.currentPage = 1
    }, 300),
    handleSeache () {
      if (!this.searchValue) {
        return
      }
      if (!this.searchValue.length || !this.tableData.length) {
        this.$message.info('找不到该标准')
        this.searchValue = ''
      }
    }
  }
}
</script>

<style scoped lang="less">
.content {
  padding: 20px;
  .card-item {
    width: 60%;
    margin: 0 auto;
    overflow: auto;
    .box-card {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
}
</style>
